.menu{
    width: 100%;
    height:800px;
    display: flex;
    background-color: antiquewhite;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.menu .menuTitle{
    font-size: 60px;
    margin-bottom: 50px;
    color: rgb(145, 42, 105);
}

.menuList{
    width:80%;
    height:auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr ;
    gap: 30px;
    place-items: center;
}
.MenuItem{
    width: 300px;
    height: 250px;
    margin-bottom: 60px;
    border-radius: 10px;
    display: flex;
    justify-content: space-evenly;
    box-shadow: 0px 3px 15px rgba(0,0,0,0.2);
}
.MenuItem:hover{
    box-shadow: 0px 3px 15px rgba(0,0,0,0.7);
    transition: 0.3s ease-in;
    cursor: pointer;
}
.MenuItem div{
    width:250px;
    height:150px;
    border-radius: 15px;
    margin-top: 10px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.MenuItem h1
{
    margin-left: 20px;
    margin-top: 160px;
}
.MenuItem h2{
    margin-left: -200px;
    margin-top: -10px;
    color: crimson;
}