.navbar{
    width:100%;
    height:80px;
    background-color:rgb(184, 100, 114);
    display: flex;
    flex-direction: row;
}
.navbar .leftSide{
    display: flex;
    align-items: center;
    padding-left: 150px;

}
.navbar .leftSide img{
    width:110px;

}
.navbar .righttSide{
    display: flex;
    justify-content: center;
    padding-left: 550px;

}
.navbar a{
    color: azure;
    text-decoration: none;
    margin: 30px;
}
 .navbar .righttSide button{
    background-color: transparent;
    border: none;
    color: white;
    cursor: pointer;
 }
 .navbar .righttSide svg{
    font-size: 40px;
 }
 @media only screen and (min-width:600px){
.navbar .righttSide button{
    display: none;
}
.hiddenLinks{
    display: none;
}
 }
 .navbar .righttSide button{
    display: inherit;
 }