.home{
    width:100%;
    height: 90vh;
    display: flex;
    justify-content: center;
    flex-direction:column;
    z-index: 1;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.headerContainer{
    display: flex;
    justify-content: space-between;

}
.headerContainer h1{
    color: black;
    font-size: 50px;
    margin-left: 120px;
    margin-top: 90px;
}
.headerContainer p{
    font-size: 25px;
    color: black;
    margin-left:-1190px;
    margin-top: 170px;

}
.button1{
    color: black;
    border: none;
    border-radius: 5px;
    margin-left: -1900px;
    margin-top: 230px;
    cursor: pointer;
    background-color: bisque;
    height: 50px;
    width: 180px;
    font-size: 20px;
    text-decoration: none;
}
.headerContainer button:hover{
    background-color:rgb(116, 104, 110);
    color: black;
    cursor: pointer;
    transition: 0.3s ease-in;

}
.header2 img{
     border-radius:50%;
     border:2px solid grey;
     width: 350px;
     height: 450px;
     margin-right: -600px;
     margin-top: -280px;
}