
.Login{
    width: 100%;
    height: 80vh;
    background-color: antiquewhite;
    display: flex;
    justify-content: center;
    align-items: center;

}
.center{
    width:25%;
    height: 400px;
    background-color: antiquewhite;
    border: 2px solid salmon;
    border-radius: 15px;

}
thead h2{
    color: rgb(145, 42, 105);
    margin-left: 30px;
}
tbody input{
    margin-left: 30px;
    border-radius: 5px;
    margin-top: 25px;
    width: 200px;
    height: 20px;
    padding: 10px;
    text-align: center;
}
input::placeholder{
    color: crimson;
}
tfoot button{
    font-size: 15px;
    width: 120px;
    height:30px;
    color: crimson;
    border-radius: 10px;
    margin-left: 20px;
    margin-top: 30px;
}
tfoot button:hover{
    background-color: coral;
}